import React from 'react';
import Block from './Block';
import Data1 from './Data1'
import './Block.css';


const HowdoesItWork = (props) => {
    return (
        <>
            <div className = "AllHD">
                <div>
                    <h1 className = "HowDoes">How does it Work</h1>
                    <br/>
                </div>
                <div className = "Matter">
                    {Data1.map((val) => {
                        return (
                            <>
                                <Block title={val.title} desc={val.Description} icon={val.icon} />
                            </>
                        );

                    })}
                </div>
            </div>
        </>
    )

}

export default HowdoesItWork;