import React from 'react';
import Data3 from './Data3';
import HowToBlock from './HowToBlock';
import './Block.css';;


const HowTo = () => {
    return (
        <>
            <div>
                <h1 className="HowToHeading">How to Send Money</h1><br></br>
                <div className="HowTo">
                    {Data3.map((val) => {
                        return (
                            <>
                                <div>
                                    <div className="col-11 col-lg-4 ml-3 work-container-subdiv cardd">
                                        <HowToBlock id={val.id} title={val.title} desc={val.desc} icon={val.icon} />
                                    </div>
                                </div>
                            </>
                        )
                    })}
                </div>
            </div>
        </>
    )
}

export default HowTo;