import React from 'react';

const Block = (props) => {
    return (
        <>

            <div>
                <div className="icon">
                    {props.icon}
                </div>
                <h3><spam><b>{props.title}</b></spam></h3>
                <p className="para2">{props.desc}</p>

            </div>

        </>
    )

}

export default Block;