import React from 'react';
import './Header.css';
import Images from './Images';
import ImageFile from './ImageFile';


const Header = (props) => {
    return (

        <div className = "whole">
            <div className="All">
                <h1 className="Header-part">Online Payment Mode <br /> Easy for you</h1>
                <section>
                    <p className="Para">
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
                    </p>
                    <div >
                        <p className="GetEarlyAccess">
                            Get Early access for You<br /><br />
                            <input type="Text" placeholder="Write Your Name" className="InputType" />
                            <button type="submit" className="Btn">Enter email</button>
                        </p>
                    </div>
                </section>
            </div>
            <div>
                {ImageFile.map((val) => {
                    return (
                        <>
                            <Images imgsrc={val.src} class={val.className} />
                        </>
                    );
                }
                )
                }
            </div>
        </div>
    )
}

export default Header;