import React from 'react';
import './Block.css';


const HowToBlock = (props) => {
    return (
        <>
            <div className="row">

                <div className="TT2">
                    <br></br>
                    <div className="icon2">{props.icon}  </div>
                    {/* <div className = "Numb2"> {props.id}</div>  */}
                    <nbsp></nbsp>   <nbsp></nbsp>
                    <div className="HowToHead">
                        {props.title}<br></br>
                    </div>
                </div>
                <p className="Para2">{props.desc}
                </p>

            </div>
        </>
    )
}

export default HowToBlock;